	position: relative;
}

.startMapOptionsContainer {
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: 1rem 0 2rem 0;
}

.startMapOptionsContainer > * {
	margin: 0.35rem;
}

.startMapOptionLabel {
	padding-top: 1rem;
}

.mapSeedOptionRow {
	display: inline-flex;
	align-items: center;
	justify-content: center;
}

.mapSeedInput {
	display: flex;
}

.rerollSeedBtn {
	width: 1.4rem;
	height: 1.4rem;
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	margin: 0.5rem;
	cursor: pointer;
}

.diceSymbol {
	transform: translate(-0%, -3%);
	font-size: 2rem;
	width: 100%;
	height: 100%;
	pointer-events: none;
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	display: flex;
	align-items: center;
	justify-content: center;
}

.startMapSliderRow {
	position: relative;
	height: auto;
	padding: 1rem;
	display: flex;
	justify-content: center;
	flex-direction: row;
	justify-content: center;
	align-items: center;
}

.colorSlider:active ~ .colorDemoBox {
	transition: 0.4s ease-in-out;
	transform: rotate(90deg);
	border-radius: 100%;
	transition-property: transform, border-radius;
}

.colorDemoBox {
	height: 2rem;
	width: 2rem;
	border: 1px solid black;
	margin: 1rem;
	transition: 0.4s ease-in-out;
	transition-property: transform, border-radius;
}

.startOptionOptionRow {
	position: relative;
	height: 1.6rem;
	padding: 1rem;
}

.startOptionOptionRow > * {
	display: inline-flex;
}

.startOptionCheckbox {
	margin-right: 0.5rem;
	vertical-align: middle;
	width: 1rem;
	height: 1rem;
}

.startOptionRowLabel {
	font-size: 0.8rem;
}

.questionBox {
	margin-left: 0.5rem;
	font-size: 1rem;
	font-weight: bolder;
	width: 1rem;
	height: 1rem;
	line-height: 1rem;
	border: 2px black solid;
	display: inline-flex;
	justify-content: center;
	vertical-align: middle;
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
}

.startOptionExplanation {
	display: flex;
	opacity: 0;
	align-items: center;
	justify-content: center;
	position: absolute;
	font-size: 0.7rem;
	border: 1px black solid;
	top: 50%;
	left: 50%;
	padding: 1rem;
	line-height: 1rem;
	transition: 0.55s cubic-bezier(0.1, 0.17, .50, 0.9);
	z-index: -3;
}

.difficultyExplanation {
	height: 11rem;
	width: 11rem;
	transform: translate(90%, -50%);
}

.visualEffectsExplanation {
	height: 7.5rem;
	width: 7.5rem;
	transform: translate(90%, -50%);
}

.visualEffectsQuestionBox:hover ~ .startOptionExplanation {
	opacity: 1;
	transform: translate(100%, -50%);
}

.difficultyQuestionBox:hover ~ .difficultyExplanation {
	opacity: 1;
	transform: translate(100%, -50%);
}

.generateMapBtn {
	height: 2.5rem;
	width: 10rem;
	margin-top: 1rem;
}

/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

